<template>
  <div class="container">
    <div class="row">
      <div class="col-lg-12">      
        <h1>Articles</h1>
      </div>
    </div>
    <div class="row">
      <div class="col-lg-11">
      </div>
      <div class="col-lg-1">
        <b-btn variant="primary" size="sm" v-if="auth">Add</b-btn>
      </div>
    </div>
    <div v-if="loading">
      Loading ...
    </div>
    <div v-if="error">
      Error. Sorry, could not load data.
    </div>
    <div class="row">
      <ArticleListItem v-for="article in articles" :key="article.id" :article="article"></ArticleListItem>
    </div>
  </div>
</template>
<script>
import axios from './../axiosProxy';
import ArticleListItem from './ArticleListItem.vue';
import { mapState } from 'vuex';

export default {
  components: { ArticleListItem },
  created() {
      this.loading = true
      axios.get('/article')
      .then((success) => {
        this.loading = false
        this.articles = success.data
        this.error = false
      })
      .catch((error) => {
        this.loading = false
        
        console.log(error)
        this.error = true;
      })
  },

  data() {
    return {
      articles: null,
      error: false,
      loading: false
    }
  },
  computed: mapState('auth', ['auth'])
}
</script>


<style scoped>
h1 {
  height: 2.5em;
  font-size: 4em;
  border-bottom: 1px solid #bbb;
  padding: 0.8em;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}
</style>
